import { FC, useMemo } from 'react';

import { Flex, Tabs, registerIcon } from '@pega/cosmos-react-core';
import * as arrowMicroLeftIcon from '@pega/cosmos-react-core/lib/components/Icon/icons/arrow-micro-left.icon';

import CaseHeader from './CaseHeader/CaseHeader';
import {
  StyledCaseSummary,
  StyledCaseSummaryInfo,
  StyledCaseSummaryContainer
} from './CaseView.styles';
import { useCaseViewContext } from './CaseView.context';
import CaseSummaryFields from './CaseSummaryFields';

registerIcon(arrowMicroLeftIcon);

const CaseSummary: FC = () => {
  const { testIds, summaryFields, summaryExpanded, tabs, aboveMD, isPreview } =
    useCaseViewContext();

  const summary = useMemo(() => {
    return <CaseSummaryFields {...summaryFields} inSummaryPanel />;
  }, [summaryFields]);

  return (
    <Flex as={StyledCaseSummary} container={{ direction: 'column' }} item={{ shrink: 0 }}>
      <CaseHeader />

      {aboveMD && !isPreview && summaryExpanded && (summary || tabs.items.length > 1) && (
        <StyledCaseSummaryInfo>
          <StyledCaseSummaryContainer>{summary}</StyledCaseSummaryContainer>

          {tabs.items.length > 1 && (
            <Tabs
              data-testid={testIds.tabs}
              type='vertical'
              tabs={tabs.items}
              onTabClick={tabs.onClick}
              currentTabId={tabs.currentTabId}
            />
          )}
        </StyledCaseSummaryInfo>
      )}
    </Flex>
  );
};

export default CaseSummary;
